/*
* 
* */
import React from 'react';
import {inject, observer} from 'mobx-react';
import {Form, Row, Col, Card, Button, message, Menu, Dropdown, Icon, Popconfirm, notification, Modal} from 'antd';

import StepInfo from "./StepInfo";
import * as utils from "@/utils/utils";
import PersonAddIcon from "@/components/worktile/PersonAddIcon";
import * as  mobx from 'mobx';
const openNotificationWithIcon = type => {
	notification[type]({
		message: '',
		description:
			'',
		placement: 'topLeft'
	});
};
@Form.create()
@observer
export default class ProjectDesign extends React.Component {
	constructor(props) {
		super(props);
		this.store= this.props.store;
		this.state = {
			userIds: [],
            current: 0,
            stepObj1:{}, //
            stepObj2:{}, //
            stepObj3:{}, //
            stepObj4:{}, //
            stepObj5:{}, //
            proDesignData:this.props.proDesign,
            hidden:true,
            defaultTree: {}, // 
			deCount: 0, // 
		};
	}

	componentWillReceiveProps(nextProps, nextContext) {
		/*
		*    taskc nextProps.data["children"][0]["children"]
		* */
		if (nextProps.proDesign!==this.props.proDesign) {
            /**/
            this.setState({proDesignData:nextProps.proDesign});
            if(Object.keys(nextProps.proDesign).length!==0&&typeof(nextProps.proDesign.children)!=="undefined"){
                this.setState({
                    stepObj1: Object.assign({},nextProps.proDesign.children.filter((item=>item.stepName==""))[0]),
                    stepObj2: Object.assign({},nextProps.proDesign.children.filter((item=>item.stepName==""))[0]),
                    stepObj3: Object.assign({},nextProps.proDesign.children.filter((item=>item.stepName==""))[0]),
                    stepObj4: Object.assign({},nextProps.proDesign.children.filter((item=>item.stepName==""))[0]),
                    stepObj5: Object.assign({},nextProps.proDesign.children.filter((item=>item.stepName==""))[0]),
                })
            }
		}
    }

    componentDidMount() {
		/*
		*    taskc nextProps.data["children"][0]["children"]
		* */
		if (this.props.proDesign) {
            /**/
            this.setState({proDesignData:this.props.proDesign});
            if(Object.keys(this.props.proDesign).length!==0&&typeof(this.props.proDesign.children)!=="undefined"){
                this.setState({
                    stepObj1: Object.assign({},this.props.proDesign.children.filter((item=>item.stepName==""))[0]),
                    stepObj2: Object.assign({},this.props.proDesign.children.filter((item=>item.stepName==""))[0]),
                    stepObj3: Object.assign({},this.props.proDesign.children.filter((item=>item.stepName==""))[0]),
                    stepObj4: Object.assign({},this.props.proDesign.children.filter((item=>item.stepName==""))[0]),
                    stepObj5: Object.assign({},this.props.proDesign.children.filter((item=>item.stepName==""))[0]),
                })
            }
		}
	}

	handleSelChange = (val, e) => {
		let userIds;
		if (e.length) {
			userIds = e.map((item) => {
				return {userId: item.props.value, userName: item.props.children}
			})
		} else {
			userIds = []
		}
		this.setState({
			userIds
		});
		this.store.proDesign.masters = Object.assign([], userIds);
	}

    // 
    updatePerson = (userIds)=>{
        const {proDesignData} = this.state;
        // 
        const currentData = Object.assign({}, proDesignData, {masters:userIds});
        // tree
        let treeData = JSON.parse(JSON.stringify(this.store.treeData));
        this.store.treeData = utils.updateTreeData(treeData, "update", currentData);
	    this.store.proDesign.masters = Object.assign([], userIds);
    }

	onChange = current => {
		this.setState({current});
	};

    // 
	handleSubmit = (e) => {
        e.preventDefault();
        const {proDesign} = this.props;
        let masters = proDesign.masters==null?[]:proDesign.masters;
        if (masters.length==0) {
            message.warn("");
            return false;
        }
        this.props.form.validateFieldsAndScroll((err, values) => {
            if (!err) {
                const {saveStage} = this.props;
                // console.log(JSON.stringify(this.state.proDesignData))
                let {children, ...proDesignStageData} = this.state.proDesignData;
                saveStage(proDesignStageData);
                this.store.isDesignEdit = true;
            }
        });
    }

    // 
    onChangeVis = () => {
        this.store.isDesignEdit = false;
        this.setState({
            defaultTree: Object.assign({}, this.state.proDesignData)
        })
	    this.props.updateCount('psCount', Number(this.props.count) + 1)
    }

    // 
    onCanlc = ()=> {
        this.props.form.resetFields();
        this.store.isDesignEdit = true;
        // 
        // tree
        let treeData = JSON.parse(JSON.stringify(this.store.treeData));
        this.store.treeData = utils.updateTreeData(treeData, "update", this.state.defaultTree);
    }

    updatedeCount= (type) => {
		if (type==true) {
			this.setState({
				deCount: this.state.deCount+1
			});
		} else {
			this.setState({
				deCount: this.state.deCount-1
			});
		}
    }

	// 
	addStage = () => {
		this.store.addStage().then((res)=>{
			if (res) {
				let oldStageId = JSON.parse(JSON.stringify(this.store.currentStageId));  // old
				this.store.oldStageId = oldStageId;
				this.store.currentStageId = res.id; // new
				this.store.getStageList();
                // tree
                let treeData = JSON.parse(JSON.stringify(this.store.treeData));
                this.store.treeData = utils.updateTreeData(treeData, "changeStage", res);
				// 
				this.store.getStageTree();
			}
		});
	}

	// 
	delStage = (param) => {
		let _this = this;
		_this.store.delStage(param).then((res)=>{
			if (res) {
				message.success("");
				let oldStageId = JSON.parse(JSON.stringify(this.store.currentStageId));
				_this.store.oldStageId = oldStageId;
				_this.store.currentStageId = res.id; // new
                // tree
                let treeData = JSON.parse(JSON.stringify(this.store.treeData));
                this.store.treeData = utils.updateTreeData(treeData, "changeStage", res);
				// 
				this.store.getStageTree();
				_this.store.getStageList();
			}
		});
	}

	// 
	changeStage = (param)=> {
		let _this = this;
        const oldStageId = JSON.parse(JSON.stringify(_this.store.currentStageId));  // old
        if (oldStageId==param) {
            openNotificationWithIcon('warning');
            return false;
        }
		if (_this.store.isDesignEdit==false) { // 
            Modal.confirm({
                title: '?',
                content: ',',
                okText:"",
                cancelText:"",
                onOk() {
                    _this.store.oldStageId = oldStageId;
                    _this.store.changeStage(param).then((res)=>{
                        if(res) {
                            _this.store.currentStageId = res.id; // new
                            _this.store.isDesignEdit = true;
                            // tree
                            let treeData = JSON.parse(JSON.stringify(_this.store.treeData));
                            _this.store.treeData = utils.updateTreeData(treeData, "changeStage", res);
	                        // 
	                        _this.store.getStageTree();
                        }
                    });
                },
                onCancel() {

                }
            });
        } else {
            _this.store.oldStageId = oldStageId;
            _this.store.changeStage(param).then((res)=>{
                if(res) {
                    _this.store.currentStageId = res.id; // new
                    // tree
                    let treeData = JSON.parse(JSON.stringify(_this.store.treeData));
                    _this.store.treeData = utils.updateTreeData(treeData, "changeStage", res);
	                // 
	                _this.store.getStageTree();
                }
            });
        }
	}

	render() {
        let _this = this;
        const {deCount, stepObj1, stepObj2, stepObj3, stepObj4, stepObj5} = _this.state;
        const {proDesign, form, store,  isShow, count,...withoutStoreProps} = this.props;
		const {getFieldDecorator} = form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 6},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 16},
			},
        };
		const {isDesignEdit, state, stageList, currentStageId} = _this.store;
		const menu = (
			<Menu className={isDesignEdit?"hidden":"tc work_menu"} style={{width:'150px'}}>
				{
					mobx.toJS(stageList).map((item, index)=>{
					   return (
						   <Menu.Item key={index} className={item.id==currentStageId?"work_menu_item work_menu_item_active":"work_menu_item"}>
							   <span onClick={this.changeStage.bind(this, item.id)}>{item.stageName}{item.indexs>=2?item.indexs:null}</span>
							   {isDesignEdit?null:<Popconfirm title="?" onConfirm={() => this.delStage(item.id)}>
								   <Icon type="close" className={item.indexs>=2?"work_menu_close":"hidden"}/>
							   </Popconfirm>}
						   </Menu.Item>
					   )
					})
				}
				{isDesignEdit?null:<Menu.Divider />}
				<Menu.Item className={isDesignEdit?"hidden":""}>
					<Icon onClick={this.addStage} style={{fontSize:'20px'}} type="plus-circle" title=""/>
				</Menu.Item>
			</Menu>
		);
		return (
			<Card bordered className="mt20" style={{width: '100%'}}>
					<h3 id={proDesign.id} className="kanban_title mb20">
						<Dropdown overlay={menu}>
						<span>
							{proDesign.indexs>=2?proDesign.indexs:null}<Icon className={isDesignEdit?"hidden":"ml5 pointer"}  type="caret-down" />
						</span>
						</Dropdown>
					</h3>
                <span className="worktile_progress_add">
                    {isDesignEdit?
                        <Button type="link"
                                className={state==2?"hidden":""}
                                disabled={isShow}
                                onClick={this.onChangeVis}>{count == 1 ? "" : ""}</Button>:
                        <div>
                            <span onClick={this.onCanlc} className="mr10"></span>
	                      <Button
		                      disabled={deCount>0?true:false}
		                      type="link" onClick={this.handleSubmit}></Button>
                        </div>
                    }
                </span>
				<Form className={count == 1 ? "hidden" : "clearfix"} layout="inline" {...formItemLayout}>
					<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]}>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="" className="w">
								{getFieldDecorator('masters', {
                                    initialValue: proDesign.masters==null?[]:proDesign.masters.map((item)=>item.userId),
									rules: [{required: false, message: ''}],
								})(
                                    <PersonAddIcon
                                        data={proDesign.masters==null?[]:proDesign.masters}
                                        updatePerson={this.updatePerson}
                                        disabled={isDesignEdit}
                                    />
								)}
							</Form.Item>
						</Col>
					</Row>
				</Form>
                <Row className={count == 1 ? "hidden" : "mt30 mb30"}>
                    <Col span={24}>
                        <StepInfo
                             key={'step1'}
                             store={this.store}
                             data={stepObj1}
                             stepName={'1. '}
                             {...withoutStoreProps}
                             disabled={isDesignEdit}
                             saveNodes={this.props.saveNodes}
                             saveAttachment={this.props.saveAttachment}
                             delNode={this.props.delNode}
                             oss={this.props.ossData}
                             updatedeCount={this.updatedeCount}
                        />
                        <StepInfo
                             key={'step2'}
                             store={this.store}
                             data={stepObj2}
                             stepName={'2. '}
                             {...withoutStoreProps}
                             disabled={isDesignEdit}
                             saveNodes={this.props.saveNodes}
                             saveAttachment={this.props.saveAttachment}
                             delNode={this.props.delNode}
                             oss={this.props.ossData}
                             updatedeCount={this.updatedeCount}
                        />
                        <StepInfo
                             key={'step3'}
                             store={this.store}
                             data={stepObj3}
                             stepName={''}
                             {...withoutStoreProps}
                             disabled={isDesignEdit}
                             saveNodes={this.props.saveNodes}
                             saveAttachment={this.props.saveAttachment}
                             delNode={this.props.delNode}
                             oss={this.props.ossData}
                             updatedeCount={this.updatedeCount}
                        />
                        <StepInfo
                             key={'step4'}
                             store={this.store}
                             data={stepObj4}
                             stepName={'3. '}
                             {...withoutStoreProps}
                             disabled={isDesignEdit}
                             saveNodes={this.props.saveNodes}
                             saveAttachment={this.props.saveAttachment}
                             delNode={this.props.delNode}
                             oss={this.props.ossData}
                             updatedeCount={this.updatedeCount}
                        />
                        <StepInfo
                             key={'step5'}
                             store={this.store}
                             data={stepObj5}
                             stepName={'4. '}
                             {...withoutStoreProps}
                             disabled={isDesignEdit}
                             saveNodes={this.props.saveNodes}
                             saveAttachment={this.props.saveAttachment}
                             delNode={this.props.delNode}
                             oss={this.props.ossData}
                             updatedeCount={this.updatedeCount}
                        />
                    </Col>
                </Row>
			</Card>
		)
	}
}




