/*
* 
* */
import React from 'react';
import {inject, observer} from 'mobx-react';
import {Input, InputNumber, Form, Row, Col, Card, Button, message, Checkbox, Tabs} from 'antd';
import MyUpload from '@/components/worktile/MyUpload';
import PersonAddIcon from "@/components/worktile/PersonAddIcon";
import * as utils from "@/utils/utils";

const {TabPane} = Tabs;
const {TextArea} = Input;
@Form.create()
@observer
export default class Appraisal extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			fields: {
				"id": '',
				"pid": '',
				"xmgs": null,
				"xmys": null,
				"xmbj": null,
				"cgrq": null,
				"ztmd": null,
				"jszq": null,
				"sjzd": null,
				"scyq": null,
				"zxyq": null,
				"dgyq": null,
				"sgclyq": null,
				"jbtsdln": null,
				"zhhsddyy": null,
				"gsxyalalck": null,
				"bcsm": null
			},
			hidden: true,  // 
			key: "1",
			currentTree:{}
		};
	}

	componentWillReceiveProps(nextProps) {
		const {itemObj} = nextProps;
		if (Object.keys(itemObj).length > 0) {
			let projectinfo = itemObj.children.filter((item) => {
				return item.treeType == "normal"
			})[0]; // 
			this.setState({
				currentTree: Object.assign({}, projectinfo),
				fields: {
					"id": projectinfo.id,
					"pid": projectinfo.pid,
					"xmgs": projectinfo.jbzl["xmgs"],
					"xmys": projectinfo.jbzl["xmys"],
					"xmbj": projectinfo.jbzl["xmbj"],
					"cgrq": projectinfo.jbzl["cgrq"],
					"ztmd": projectinfo.jbzl["ztmd"],
					"jszq": projectinfo.jbzl["jszq"],
					"sjzd": projectinfo.srzl["sjzd"],
					"scyq": projectinfo.srzl["scyq"],
					"zxyq": projectinfo.srzl["zxyq"],
					"dgyq": projectinfo.srzl["dgyq"],
					"sgclyq": projectinfo.srzl["sgclyq"],
					"jbtsdln": projectinfo.srzl["jbtsdln"],
					"zhhsddyy": projectinfo.srzl["zhhsddyy"],
					"gsxyalalck": projectinfo.fzzl["gsxyalalck"],
					"bcsm": projectinfo.fzzl["bcsm"],
				},
			});
		}
	}

	componentDidMount() {
		const {itemObj, disabled} = this.props;
		if (Object.keys(itemObj).length > 0) {
			let projectinfo = itemObj.children.filter((item) => {
				return item.treeType == "normal"
			})[0]; // 
			this.setState({
				currentTree: Object.assign({}, projectinfo),
				fields: {
					"id": projectinfo.id,
					"pid": projectinfo.pid,
					"xmgs": projectinfo.jbzl["xmgs"],
					"xmys": projectinfo.jbzl["xmys"],
					"xmbj": projectinfo.jbzl["xmbj"],
					"cgrq": projectinfo.jbzl["cgrq"],
					"ztmd": projectinfo.jbzl["ztmd"],
					"jszq": projectinfo.jbzl["jszq"],
					"sjzd": projectinfo.srzl["sjzd"],
					"scyq": projectinfo.srzl["scyq"],
					"zxyq": projectinfo.srzl["zxyq"],
					"dgyq": projectinfo.srzl["dgyq"],
					"sgclyq": projectinfo.srzl["sgclyq"],
					"jbtsdln": projectinfo.srzl["jbtsdln"],
					"zhhsddyy": projectinfo.srzl["zhhsddyy"],
					"gsxyalalck": projectinfo.fzzl["gsxyalalck"],
					"bcsm": projectinfo.fzzl["bcsm"],
				},
				hidden: disabled
			});
		}
		this.props.onAppRef(this);
	}

	// 
	handleSubmit = (e) => {
		e.preventDefault();
		let _this = this;
		const {projectId} = _this.store;
		_this.props.form.validateFields((err, values) => {
			//console.log(values, err, '')
			if (!err) {
				let currentTree = JSON.parse(JSON.stringify(_this.state.currentTree));
				const {fields}=_this.state;
				const params = Object.assign(currentTree, {
					"projectId": projectId,
					"jbzl": {
						"xmgs": values["xmgs"],
						"xmys": values["xmys"],
						"xmbj": values["xmbj"],
						"cgrq": values["cgrq"],
						"ztmd": values["ztmd"],
						"jszq": values["jszq"]
					},
					"srzl": {
						"sjzd": values["sjzd"],
						"scyq": values["scyq"],
						"zxyq": values["zxyq"],
						"dgyq": values["dgyq"],
						"sgclyq": values["sgclyq"],
						"jbtsdln": values["jbtsdln"],
						"zhhsddyy": fields["zhhsddyy"]
					},
					"fzzl": {
						"gsxyalalck": values["gsxyalalck"],
						"bcsm": values["bcsm"],
					}
				});
				_this.props.saveNodes(params);
				_this.setState({
					hidden: !_this.state.hidden,
					key: '1'
				});
			} else {
				message.warn("")
				return false;
			}
		});
	}

	// 
	updatePerson = (type, userIds) => {
		let fields = JSON.parse(JSON.stringify(this.state.fields));
		this.setState({
			fields: Object.assign(fields, {
				[type]: userIds
			})
		});
	}

	// 
	onChangeVis = () => {
		this.setState({
			hidden: !this.state.hidden,
		});
		this.props.updateCount('pgCount', Number(this.props.count) + 1)
	}

	// 
	onCanlc = () => {
		this.props.form.resetFields();
		this.setState({
			hidden: !this.state.hidden
		});
	}

	callback = (key) => {
		this.setState({
			key
		});
	}

	onChange = (type, checkedValues) => {
		let fields = JSON.parse(JSON.stringify(this.state.fields));
		this.setState({
			fields: Object.assign(fields, {[type]: checkedValues})
		});
	}

	// 
	updateMaster= (userIds)=>{
		const {currentTree} = this.state;
		// 
		const currentData = Object.assign({}, currentTree, {masters:userIds});
		// tree
		let treeData = JSON.parse(JSON.stringify(this.store.treeData));
		this.store.treeData = utils.updateTreeData(treeData, "update", currentData);
	}

	render() {
		const {fields, hidden, key, currentTree} = this.state;
		const {state} = this.store;
		const {count, isShow, ossData, saveAttachment, delNode, itemObj} = this.props;
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 6},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 18},
			},
		};
		const formItemLayout2 = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 6},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 16},
			},
		};
		let attcObj = currentTree.children != null && currentTree.children.filter((item) => {
			return item.treeType === 'attc'&&item.name=="CAD"
		})[0];  // CAD
		let attcObj2 = currentTree.children != null && currentTree.children.filter((item) => {
			return item.treeType === 'attc'&&item.name=="/"
		})[0];  // 
		const attachments =attcObj.children != null ? attcObj.children.map((vitem) => {
			return Object.assign({}, {
				uid: vitem.id,
				name: vitem.fileName,
				status: 'done',
				url: vitem.filePath,
				fileId: vitem.fileId,
				fileName: vitem.fileName,
				filePath: vitem.filePath,
				id: vitem.id
			})
		}) : [];
		const attachments2 = attcObj2.children != null ? attcObj2.children.map((vitem) => {
			return Object.assign({}, {
				uid: vitem.id,
				name: vitem.fileName,
				status: 'done',
				url: vitem.filePath,
				fileId: vitem.fileId,
				fileName: vitem.fileName,
				filePath: vitem.filePath,
				id: vitem.id
			})
		}) : [];
		const plainOptions = ['', 'BIM', '', '', 'VR', ''];
		return (
			<Card bordered className="pr mt20" style={{width: '100%'}}>
				<h3 id={itemObj.id} className="kanban_title mb20"></h3>
				<span className="worktile_progress_add">
                     {hidden ?
	                     <Button className={state == 2 ? "hidden" : ""} type="link"
	                             disabled={isShow}
	                             onClick={this.onChangeVis}>{count == 1 ? "" : ""}</Button> :
	                     <div>
		                     <span onClick={this.onCanlc} className="mr10"></span>
		                     <span onClick={this.handleSubmit}></span>
	                     </div>}
                </span>
				<Form className={count==1 ? "hidden" : "clearfix"} layout="inline" {...formItemLayout2}>
					<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]}>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="" className="w">
								{getFieldDecorator('masters', {
									initialValue: currentTree&&currentTree.masters==null?[]:currentTree.masters.map(item=>item.userId),
									rules: [{required: false, message: ''}],
								})(
									<PersonAddIcon
										data={currentTree&&currentTree.masters==null?[]:currentTree.masters}
										updatePerson={this.updateMaster}
										disabled={hidden}
									/>
								)}
							</Form.Item>
						</Col>
					</Row>
				</Form>
				<Tabs className={count == 1 ? "hidden" : "item_tabs"} activeKey={key} onChange={this.callback}>
					<TabPane tab="" key="1">
						<Form layout="inline" {...formItemLayout}>
							<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]} style={{margin: "0"}}>
								<Col xs={24} sm={24} md={12} lg={12} xl={12}>
									<Form.Item label="" className="w">
										{getFieldDecorator('xmgs', {
											initialValue: fields.xmgs || "",
											rules: [{required: true, message: ''}],
										})(
											<Input placeholder="" disabled={hidden}/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={12} lg={12} xl={12}>
									<Form.Item label="()" className="w">
										{getFieldDecorator('xmys', {
											initialValue: fields.xmys || "",
											rules: [{required: true, message: ''}],
										})(
											<InputNumber placeholder="" disabled={hidden} className="w"/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={12} lg={12} xl={12}>
									<Form.Item label="" className="w">
										{getFieldDecorator('xmbj', {
											initialValue: fields.xmbj || "",
											rules: [{required: true, message: ''}],
										})(
											<TextArea rows={4} disabled={hidden} placeholder=""/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={12} lg={12} xl={12}>
									<Form.Item label="" className="w">
										{getFieldDecorator('cgrq', {
											initialValue: fields.cgrq || "",
											rules: [{required: true, message: ''}],
										})(
											<TextArea rows={4} disabled={hidden} placeholder=""/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={12} lg={12} xl={12}>
									<Form.Item label="" className="w">
										{getFieldDecorator('ztmd', {
											initialValue: fields.ztmd || "",
											rules: [{required: false, message: ''}],
										})(
											<TextArea rows={4} disabled={hidden} placeholder=""/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={12} lg={12} xl={12}>
									<Form.Item label="" className="w">
										{getFieldDecorator('jszq', {
											initialValue: fields.jszq || "",
											rules: [{required: false, message: ''}],
										})(
											<TextArea rows={4} disabled={hidden} placeholder=""/>
										)}
									</Form.Item>
								</Col>
							</Row>
						</Form>
					</TabPane>
					<TabPane tab="" key="2">
						<Form layout="inline" {...formItemLayout}>
							<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]} style={{margin: "0"}}>
								<Col xs={24} sm={24} md={12} lg={12} xl={12}>
									<Form.Item label="()" className="w">
										{getFieldDecorator('sjzd', {
											initialValue: fields.sjzd || "",
											rules: [{required: false, message: ''}],
										})(
											<TextArea rows={4} disabled={hidden} placeholder=""/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={12} lg={12} xl={12}>
									<Form.Item label="" className="w">
										{getFieldDecorator('scyq', {
											initialValue: fields.scyq || "",
											rules: [{required: false, message: ''}],
										})(
											<TextArea rows={4} disabled={hidden} placeholder=""/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={12} lg={12} xl={12}>
									<Form.Item label="" className="w">
										{getFieldDecorator('zxyq', {
											initialValue: fields.zxyq || "",
											rules: [{required: false, message: ''}],
										})(
											<TextArea rows={4} disabled={hidden} placeholder=""/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={12} lg={12} xl={12}>
									<Form.Item label="" className="w">
										{getFieldDecorator('dgyq', {
											initialValue: fields.dgyq || "",
											rules: [{required: false, message: ''}],
										})(
											<TextArea rows={4} disabled={hidden} placeholder=""/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={12} lg={12} xl={12}>
									<Form.Item label="" className="w">
										{getFieldDecorator('sgclyq', {
											initialValue: fields.sgclyq || "",
											rules: [{required: false, message: ''}],
										})(
											<TextArea rows={4} disabled={hidden} placeholder=""/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={12} lg={12} xl={12}>
									<Form.Item label="" className="w">
										{getFieldDecorator('jbtsdln', {
											initialValue: fields.jbtsdln || "",
											rules: [{required: false, message: ''}],
										})(
											<TextArea rows={4} disabled={hidden} placeholder=""/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={24} lg={24} xl={24}>
									<Form.Item label=""
									           labelCol={{
										           xs: {span: 24},
										           sm: {span: 3},
									           }}
									           wrapperCol={{
										           xs: {span: 24},
										           sm: {span: 20},
									           }}
									           className="w">
										<Checkbox.Group
											//disabled={hidden}
											className={hidden?"checkbox_dis":""}
											onChange={hidden?null:this.onChange.bind(this, 'zhhsddyy')}
											value={fields.zhhsddyy==null?[]:fields.zhhsddyy}
											options={plainOptions}/>
									</Form.Item>
								</Col>
							</Row>
						</Form>
					</TabPane>
					<TabPane tab="" key="3">
						<Form layout="inline" {...formItemLayout}>
							<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]} style={{margin: "0"}}>
								<Col xs={24} sm={24} md={24} lg={24} xl={24}>
									<Form.Item label="/"
									           labelCol={{
										           xs: {span: 24},
										           sm: {span: 4},
									           }}
									           wrapperCol={{
										           xs: {span: 24},
										           sm: {span: 20},
									           }}
									           className="w">
										{getFieldDecorator('gsxyalalck', {
											initialValue: fields.gsxyalalck || "",
											rules: [{required: false, message: ''}],
										})(
											<TextArea rows={4} disabled={hidden} placeholder=""/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={24} lg={24} xl={24}>
									<Form.Item label=""
									           labelCol={{
										           xs: {span: 24},
										           sm: {span: 4},
									           }}
									           wrapperCol={{
										           xs: {span: 24},
										           sm: {span: 20},
										           offset: 3
									           }}
									           className="w">
										<div className={"pr"}>
											<MyUpload
												saveAttachment={saveAttachment}
												delNode={delNode}
												fileList={attachments2}
												disabled={hidden}
												pid={attcObj2.id}
												projectId={attcObj2.projectId}
												ossData={ossData}
											/>
											<span style={{
												position: 'absolute',
												left: '35px',
												top: '-5px',
												fontSize: '12px',
												color: '#999'
											}}></span>
										</div>
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={24} lg={24} xl={24}>
									<Form.Item label=""
									           labelCol={{
										           xs: {span: 24},
										           sm: {span: 4},
									           }}
									           wrapperCol={{
										           xs: {span: 24},
										           sm: {span: 20},
									           }}
									           className="w">
										{getFieldDecorator('bcsm', {
											initialValue: fields.bcsm || "",
											rules: [{required: false, message: ''}],
										})(
											<TextArea rows={4} disabled={hidden} placeholder=""/>
										)}
									</Form.Item>
								</Col>
							</Row>
						</Form>
					</TabPane>
				</Tabs>
				<Row className={key == "3"||count == 1 ? "hidden" : ""}>
					<Col xs={24} sm={24} md={24} lg={24} xl={24}>
						<Form.Item label="CAD"
						           labelCol={{
							           xs: {span: 24},
							           sm: {span: 4},
						           }}
						           wrapperCol={{
							           xs: {span: 24},
							           sm: {span: 20},
						           }}
						           className="w">
							<MyUpload
								saveAttachment={saveAttachment}
								delNode={delNode}
								fileList={attachments}
								disabled={hidden}
								pid={attcObj.id}
								projectId={attcObj.projectId}
								ossData={ossData}
							/>
						</Form.Item>
					</Col>
				</Row>
			</Card>
		)
	}
}



