/*
* 
* */
import React from 'react';
import {inject, observer} from 'mobx-react';
import {Input, InputNumber, Form, Row, Col, Card, Button, Icon, Checkbox, Tabs, Radio, DatePicker} from 'antd';
import MyUpload from '@/components/worktile/MyUpload';
import moment from 'moment';
import PersonAddIcon from "@/components/worktile/PersonAddIcon";
import * as utils from "@/utils/utils";
const {TabPane} = Tabs;
const {TextArea} = Input;

@Form.create()
@observer
export default class Office extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			glry: [{"name": "", "checked": false, "values": []}, {
				"name": "",
				"checked": false,
				"values": []
			}, {"name": "(UB)", "checked": false, "values": []}, {
				"name": "",
				"checked": false,
				"values": []
			}, {"name": "", "checked": false, "values": []}, {
				"name": "",
				"checked": false,
				"values": []
			}, {"name": "", "checked": false, "values": []}, {"name": "", "checked": false, "values": []}],
			lwry: [{"name": "", "checked": false, "values": []}, {"name": "", "checked": false, "values": []}, {
				"name": "", "checked": false,
				"values": []
			}, {"name": "", "checked": false, "values": []}, {"name": "", "checked": false, "values": []}, {
				"name": "", "checked": false,
				"values": []
			}, {"name": "", "checked": false, "values": []}, {"name": "", "checked": false, "values": []}],
			fields: {
				"id": "",
				"pid": "",
				"cqwd": [],
				"zhhsddyy": null,
				"bghjdssd": null,
				"jbtedln": null,
				"jzmgsxym": null,
				"jbdfts": null,
				"bimxxhsdyq": null,
				"gmxqdg": null,
				"dlqk": null,
				"rksd": null,
				"zwhj": null,
				"ljjcsj": 0,
				"gsxyalalcg": null
			},
			hidden: true,  // 
			key: "1",
			currentTree: {}
		};
	}

	componentWillReceiveProps(nextProps) {
		const {itemObj} = nextProps;
		if (Object.keys(itemObj).length > 0) {
			let projectinfo = itemObj.children.filter((item) => {
				return item.treeType == "normal"
			})[0]; // 
			this.setState({
				currentTree: Object.assign({}, projectinfo),
				fields: {
					"id": projectinfo.id,
					"pid": projectinfo.pid,
					"cqwd": projectinfo.jbzl["cqwd"],
					"zhhsddyy": projectinfo.srzl["zhhsddyy"],
					"bghjdssd": projectinfo.srzl["bghjdssd"],
					"jbtedln": projectinfo.srzl["jbtedln"],
					"jzmgsxym": projectinfo.srzl["jzmgsxym"],
					"jbdfts": projectinfo.srzl["jbdfts"],
					"bimxxhsdyq": projectinfo.srzl["bimxxhsdyq"],
					"gmxqdg": projectinfo.srzl["gmxqdg"],
					"dlqk": projectinfo.fzzl["dlqk"],
					"rksd": projectinfo.fzzl["rksd"],
					"zwhj": projectinfo.fzzl["zwhj"],
					"ljjcsj": projectinfo.fzzl["ljjcsj"],
					"gsxyalalcg": projectinfo.fzzl["gsxyalalcg"],
				},
				glry: projectinfo.jbzl["glry"].map((item) => {
					return Object.assign({}, {
						"name": item.name,
						"values": item.values == null ? [] : item.values,
						"checked": item.values != null && Array.isArray(item.values) && item.values.length > 0 ? true : false
					})
				}),
				lwry: projectinfo.jbzl["lwry"].map((item) => {
					return Object.assign({}, {
						"name": item.name,
						"values": item.values == null ? [] : item.values,
						"checked": item.values != null && Array.isArray(item.values) && item.values.length > 0 ? true : false
					})
				}),
			});
		}
	}

	componentDidMount() {
		const {itemObj, disabled} = this.props;
		if (Object.keys(itemObj).length > 0) {
			let projectinfo = itemObj.children.filter((item) => {
				return item.treeType == "normal"
			})[0]; // 
			//console.log(projectinfo, 'projectinfo')
			this.setState({
				currentTree: Object.assign({}, projectinfo),
				fields: {
					"id": projectinfo.id,
					"pid": projectinfo.pid,
					"cqwd": projectinfo.jbzl["cqwd"],
					"zhhsddyy": projectinfo.srzl["zhhsddyy"],
					"bghjdssd": projectinfo.srzl["bghjdssd"],
					"jbtedln": projectinfo.srzl["jbtedln"],
					"jzmgsxym": projectinfo.srzl["jzmgsxym"],
					"jbdfts": projectinfo.srzl["jbdfts"],
					"bimxxhsdyq": projectinfo.srzl["bimxxhsdyq"],
					"gmxqdg": projectinfo.srzl["gmxqdg"],
					"dlqk": projectinfo.fzzl["dlqk"],
					"rksd": projectinfo.fzzl["rksd"],
					"zwhj": projectinfo.fzzl["zwhj"],
					"ljjcsj": projectinfo.fzzl["ljjcsj"],
					"gsxyalalcg": projectinfo.fzzl["gsxyalalcg"],
				},
				hidden: disabled,
				glry: projectinfo.jbzl["glry"].map((item) => {
					return Object.assign({}, {
						"name": item.name,
						"values": item.values == null ? [] : item.values,
						"checked": false
					})
				}),
				lwry: projectinfo.jbzl["lwry"].map((item) => {
					return Object.assign({}, {
						"name": item.name,
						"values": item.values == null ? [] : item.values,
						"checked": false
					})
				}),
			});
		}
		this.props.onOfficeRef(this);
	}

	// 
	handleSubmit = (e) => {
		e.preventDefault();
		let _this = this;
		const {projectId} = _this.store;
		_this.props.form.validateFields((err, values) => {
			let fields = JSON.parse(JSON.stringify(_this.state.fields));
			let glry = JSON.parse(JSON.stringify(_this.state.glry));
			let lwry = JSON.parse(JSON.stringify(_this.state.lwry));
			if (!err) {
				let currentTree = JSON.parse(JSON.stringify(_this.state.currentTree));
				const glRY = glry.map(item => {
					return Object.assign({}, {name: item.name, values: item.values})
				})
				const lwRY = lwry.map(item => {
					return Object.assign({}, {name: item.name, values: item.values})
				})
				const params = Object.assign(currentTree, {
					"projectId": projectId,
					"jbzl": {
						"glry": Object.assign([], glRY),
						"lwry": Object.assign([], lwRY),
						"cqwd": fields.cqwd
					},
					"srzl": {
						"zhhsddyy": values.zhhsddyy,
						"bghjdssd": values.bghjdssd,
						"jbtedln": values.jbtedln,
						"jzmgsxym": values.jzmgsxym,
						"jbdfts": values.jbdfts,
						"bimxxhsdyq": values.bimxxhsdyq,
						"gmxqdg": values.gmxqdg,
					},
					"fzzl": {
						"dlqk": values.dlqk,
						"rksd": values.rksd,
						"zwhj": values.zwhj,
						"ljjcsj": values['ljjcsj'] == null || values['ljjcsj'] == "" ? 0 : moment(values['ljjcsj'].format('YYYY-MM-DD'), 'YYYY-MM-DD').valueOf(),
						"gsxyalalcg": values.gsxyalalcg,
					}
				});
				_this.props.saveNodes(params);
				_this.setState({
					hidden: !_this.state.hidden,
					key: '1'
				});
			}
		});
	}

	// 
	updatePerson = (type, userIds) => {
		let fields = JSON.parse(JSON.stringify(this.state.fields));
		this.setState({
			fields: Object.assign(fields, {
				[type]: userIds
			})
		});
	}

	// 
	onChangeVis = () => {
		this.setState({
			hidden: !this.state.hidden,
		});
		this.props.updateCount('pgCount', Number(this.props.count) + 1)
	}

	// 
	onCanlc = () => {
		this.props.form.resetFields();
		this.setState({
			hidden: !this.state.hidden
		});
	}

	callback = (key) => {
		this.setState({
			key
		});
	}

	checkChange = (type, name, e) => {
		let glry = JSON.parse(JSON.stringify(this.state[type]));
		const index = glry.findIndex((item) => item.name === name);
		const {checked} = e.target;
		glry[index].checked = checked;
		if (checked == false) {
			glry[index].values = Object.assign([]);
		}
		this.setState({
			[type]: Object.assign([], glry)
		});
	}

	onNameChange = (type, name, checkedValues) => {
		let glry = JSON.parse(JSON.stringify(this.state[type]));
		const index = glry.findIndex((item) => item.name === name);
		glry[index].values = Object.assign([], checkedValues);
		this.setState({
			[type]: Object.assign([], glry)
		}, () => {

		});
	}

	onChange = (type, checkedValues) => {
		let fields = JSON.parse(JSON.stringify(this.state.fields));
		this.setState({
			fields: Object.assign(fields, {[type]: checkedValues})
		});
	}

	// 
	updateMaster= (userIds)=>{
		const {currentTree} = this.state;
		// 
		const currentData = Object.assign({}, currentTree, {masters:userIds});
		// tree
		let treeData = JSON.parse(JSON.stringify(this.store.treeData));
		this.store.treeData = utils.updateTreeData(treeData, "update", currentData);
	}

	render() {
		const {fields, hidden, key, glry, lwry, currentTree} = this.state;
		const {state} = this.store;
		const {count, isShow, ossData, saveAttachment, delNode, itemObj} = this.props;
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 7},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 16},
			},
		};
		const formItemLayout2 = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 6},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 16},
			},
		};
		const plainOptions = ['', '', '', ''];
		const Coptions = ['', '', ''];
		let attcObj = currentTree.children != null && currentTree.children.filter((item) => {
			return item.treeType === 'attc' && item.name == ""
		})[0];  // 
		let attcObj2 = currentTree.children != null && currentTree.children.filter((item) => {
			return item.treeType === 'attc' && item.name == ""
		})[0];  // 
		let attcObj3 = currentTree.children != null && currentTree.children.filter((item) => {
			return item.treeType === 'attc' && item.name == "/"
		})[0];  // 
		const attachments = attcObj.children != null ? attcObj.children.map((vitem) => {
			return Object.assign({}, {
				uid: vitem.id,
				name: vitem.fileName,
				status: 'done',
				url: vitem.filePath,
				fileId: vitem.fileId,
				fileName: vitem.fileName,
				filePath: vitem.filePath,
				id: vitem.id
			})
		}) : [];
		const attachments2 = attcObj2.children != null ? attcObj2.children.map((vitem) => {
			return Object.assign({}, {
				uid: vitem.id,
				name: vitem.fileName,
				status: 'done',
				url: vitem.filePath,
				fileId: vitem.fileId,
				fileName: vitem.fileName,
				filePath: vitem.filePath,
				id: vitem.id
			})
		}) : [];
		const attachments3 = attcObj3.children != null ? attcObj3.children.map((vitem) => {
			return Object.assign({}, {
				uid: vitem.id,
				name: vitem.fileName,
				status: 'done',
				url: vitem.filePath,
				fileId: vitem.fileId,
				fileName: vitem.fileName,
				filePath: vitem.filePath,
				id: vitem.id
			})
		}) : [];
		return (
			<Card bordered className="pr mt20" style={{width: '100%'}}>
				<h3 id={itemObj.id} className="kanban_title mb20"></h3>
				<span className="worktile_progress_add">
                     {hidden ?
	                     <Button className={state == 2 ? "hidden" : ""} type="link"
	                             disabled={isShow}
	                             onClick={this.onChangeVis}>{count == 1 ? "" : ""}</Button> :
	                     <div>
		                     <span onClick={this.onCanlc} className="mr10"></span>
		                     <span onClick={this.handleSubmit}></span>
	                     </div>}
                </span>
				<Form className={count==1 ? "hidden" : "clearfix"} layout="inline" {...formItemLayout2}>
					<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]}>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="" className="w">
								{getFieldDecorator('masters', {
									initialValue: currentTree&&currentTree.masters==null?[]:currentTree.masters.map(item=>item.userId),
									rules: [{required: false, message: ''}],
								})(
									<PersonAddIcon
										data={currentTree&&currentTree.masters==null?[]:currentTree.masters}
										updatePerson={this.updateMaster}
										disabled={hidden}
									/>
								)}
							</Form.Item>
						</Col>
					</Row>
				</Form>
				<Tabs className={count == 1 ? "hidden" : "item_tabs"} activeKey={key} onChange={this.callback}>
					<TabPane tab="" key="1">
						<Form layout="inline" {...formItemLayout}>
							<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]} style={{margin: "0"}}>
								<Col xs={24} sm={24} md={24} lg={24} xl={24}>
									<Form.Item
										labelCol={{
											xs: {span: 24},
											sm: {span: 6},
										}}
										wrapperCol={{
											xs: {span: 24},
											sm: {span: 12},
										}}
										labelAlign={"left"}
										label="" className="w"></Form.Item>
								</Col>
								<Col xs={24} sm={24} md={12} lg={12} xl={12}>
									<Form.Item label=""
									           labelCol={{
										           xs: {span: 24},
										           sm: {span: 4},
									           }}
									           wrapperCol={{
										           xs: {span: 24},
										           sm: {span: 20},
									           }}
									           className="w">
										<div>
											{glry.length > 0 && glry.map((item, index) => {
												return (<div key={'glry' + index} style={{paddingLeft: '10px'}}>
													<Checkbox
														className={hidden?"checkbox_dis lwry_checkbox":"lwry_checkbox"}
														style={{width: '118px'}}
														checked={item.checked}
														//disabled={hidden}
														onChange={hidden?null:this.checkChange.bind(this, 'glry', item.name)}>{item.name}</Checkbox>
													<Checkbox.Group
														className={item.checked ? (hidden?"checkbox_dis":"") : "hidden"}
														value={item.values}
														onChange={hidden?null:this.onNameChange.bind(this, 'glry', item.name)}
														//disabled={hidden}
														options={plainOptions}/>
												</div>)
											})}
										</div>
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={12} lg={12} xl={12}>
									<Form.Item label=""
									           labelCol={{
										           xs: {span: 24},
										           sm: {span: 4},
									           }}
									           wrapperCol={{
										           xs: {span: 24},
										           sm: {span: 20},
									           }}
									           className="w">
										{lwry.length > 0 && lwry.map((item, index) => {
											return (<div key={'lwry' + index} style={{paddingLeft: '10px'}}>
												<Checkbox
													className={hidden?"checkbox_dis lwry_checkbox":"lwry_checkbox"}
													style={{width: '95px'}}
													checked={item.checked}
													//disabled={hidden}
													onChange={hidden?null:this.checkChange.bind(this, 'lwry', item.name)}>{item.name}</Checkbox>
												<Checkbox.Group
													className={item.checked ? (hidden?"checkbox_dis":"") : "hidden"}
													value={item.values}
													onChange={hidden?null:this.onNameChange.bind(this, 'lwry', item.name)}
													//disabled={hidden}
													options={plainOptions}/>
											</div>)
										})}
									</Form.Item>
								</Col>
							</Row>
							<Row>
								<Col xs={24} sm={24} md={12} lg={12} xl={12}>
									<Form.Item label=""
									           labelCol={{
										           xs: {span: 24},
										           sm: {span: 4},
									           }}
									           wrapperCol={{
										           xs: {span: 24},
										           sm: {span: 20},
									           }}
									           className="w">
										<Checkbox.Group
											className={hidden?"checkbox_dis":""}
											onChange={hidden?null:this.onChange.bind(this, 'cqwd')}
											//disabled={hidden}
											value={fields.cqwd}
											options={Coptions}/>
									</Form.Item>
								</Col>
							</Row>
						</Form>
					</TabPane>
					<TabPane tab="" key="2">
						<Form layout="inline" {...formItemLayout}>
							<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]} style={{margin: "0"}}>
								<Col xs={24} sm={24} md={12} lg={12} xl={12}>
									<Form.Item label="" className="w">
										{getFieldDecorator('zhhsddyy', {
											initialValue: fields.zhhsddyy || "",
											rules: [{required: false, message: ''}],
										})(
											<TextArea rows={4} disabled={hidden} placeholder=""/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={12} lg={12} xl={12}>
									<Form.Item label="" className="w">
										{getFieldDecorator('bghjdssd', {
											initialValue: fields.bghjdssd || "",
											rules: [{required: false, message: ''}],
										})(
											<TextArea rows={4} disabled={hidden} placeholder=""/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={12} lg={12} xl={12}>
									<Form.Item label="" className="w">
										{getFieldDecorator('jbtedln', {
											initialValue: fields.jbtedln || "",
											rules: [{required: false, message: ''}],
										})(
											<TextArea rows={4} disabled={hidden} placeholder=""/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={12} lg={12} xl={12}>
									<Form.Item label="" className="w">
										{getFieldDecorator('jzmgsxym', {
											initialValue: fields.jzmgsxym || "",
											rules: [{required: false, message: ''}],
										})(
											<TextArea rows={4} disabled={hidden} placeholder=""/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={12} lg={12} xl={12}>
									<Form.Item label="" className="w">
										{getFieldDecorator('jbdfts', {
											initialValue: fields.jbdfts || "",
											rules: [{required: false, message: ''}],
										})(
											<TextArea rows={4} disabled={hidden} placeholder=""/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={12} lg={12} xl={12}>
									<Form.Item label="BIM" className="w">
										{getFieldDecorator('bimxxhsdyq', {
											initialValue: fields.bimxxhsdyq || "",
											rules: [{required: false, message: ''}],
										})(
											<TextArea rows={4} disabled={hidden} placeholder=""/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={12} lg={12} xl={12}>
									<Form.Item label="" className="w">
										{getFieldDecorator('gmxqdg', {
											initialValue: fields.gmxqdg || "",
											rules: [{required: false, message: ''}],
										})(
											<TextArea rows={4} disabled={hidden} placeholder=""/>
										)}
									</Form.Item>
								</Col>
							</Row>
						</Form>
					</TabPane>
					<TabPane tab="" key="3">
						<Form layout="inline" {...formItemLayout}>
							<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]} style={{margin: "0"}}>
								<Col xs={24} sm={24} md={24} lg={24} xl={24}>
									<Form.Item
										labelCol={{
											xs: {span: 24},
											sm: {span: 3},
										}}
										wrapperCol={{
											xs: {span: 24},
											sm: {span: 20},
										}}
										label="" className="w"></Form.Item>
								</Col>
								<Col xs={24} sm={24} md={8} lg={8} xl={8}>
									<Form.Item label="" className="w">
										{getFieldDecorator('dlqk', {
											initialValue: fields.dlqk || "",
											rules: [{required: false, message: ''}],
										})(
											<TextArea rows={4} disabled={hidden} placeholder=""/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={8} lg={8} xl={8}>
									<Form.Item label="" className="w">
										{getFieldDecorator('rksd', {
											initialValue: fields.rksd || "",
											rules: [{required: false, message: ''}],
										})(
											<TextArea rows={4} disabled={hidden} placeholder=""/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={8} lg={8} xl={8}>
									<Form.Item label="" className="w">
										{getFieldDecorator('zwhj', {
											initialValue: fields.zwhj || "",
											rules: [{required: false, message: ''}],
										})(
											<TextArea rows={4} disabled={hidden} placeholder=""/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={24} lg={24} xl={24}>
									<Form.Item label=""
									           labelCol={{
										           xs: {span: 24},
										           sm: {span: 4},
									           }}
									           wrapperCol={{
										           xs: {span: 24},
										           sm: {span: 20},
									           }}
									           className="w">
										<MyUpload
											saveAttachment={saveAttachment}
											delNode={delNode}
											fileList={attachments}
											disabled={hidden}
											pid={attcObj.id}
											projectId={attcObj.projectId}
											ossData={ossData}
										/>
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={24} lg={24} xl={24}>
									<Form.Item label=""
									           labelCol={{
										           xs: {span: 24},
										           sm: {span: 4},
									           }}
									           wrapperCol={{
										           xs: {span: 24},
										           sm: {span: 20},
									           }}
									           className="w">
										<MyUpload
											saveAttachment={saveAttachment}
											delNode={delNode}
											fileList={attachments2}
											disabled={hidden}
											pid={attcObj2.id}
											projectId={attcObj2.projectId}
											ossData={ossData}
										/>
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={24} lg={24} xl={24}>
									<Form.Item label=""
									           labelCol={{
										           xs: {span: 24},
										           sm: {span: 4},
									           }}
									           wrapperCol={{
										           xs: {span: 24},
										           sm: {span: 16},
									           }}
									           className="w">
										{getFieldDecorator('ljjcsj', {
											initialValue: fields.ljjcsj == null || fields.ljjcsj == 0 ? "" : moment(moment(fields.ljjcsj).format('YYYY-MM-DD'), "YYYY-MM-DD"),
											rules: [{required: false, message: 'CAD'}],
										})(
											<DatePicker
												placeholder=""
												format="YYYY-MM-DD"
												disabled={hidden}
											/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={24} lg={24} xl={24}>
									<Form.Item label="/"
									           labelCol={{
										           xs: {span: 24},
										           sm: {span: 4},
									           }}
									           wrapperCol={{
										           xs: {span: 24},
										           sm: {span: 16},
									           }}
									           className="w">
										{getFieldDecorator('gsxyalalcg', {
											initialValue: fields.gsxyalalcg || "",
											rules: [{required: false, message: 'CAD'}],
										})(
											<TextArea rows={4} disabled={hidden}
											          placeholder="///"/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={24} lg={24} xl={24}>
									<Form.Item label=""
									           labelCol={{
										           xs: {span: 24},
										           sm: {span: 3},
									           }}
									           wrapperCol={{
										           xs: {span: 24},
										           sm: {span: 20},
										           offset: 2
									           }}
									           className="w">
										<div className={"pr"}>
											<MyUpload
												saveAttachment={saveAttachment}
												delNode={delNode}
												fileList={attachments3}
												disabled={hidden}
												pid={attcObj3.id}
												projectId={attcObj3.projectId}
												ossData={ossData}
											/>
											<span style={{
												position: 'absolute',
												left: '35px',
												top: '-5px',
												fontSize: '12px',
												color: '#999'
											}}></span>
										</div>
									</Form.Item>
								</Col>
							</Row>
						</Form>
					</TabPane>
				</Tabs>
			</Card>
		)
	}
}



